import React from "react";

import "./inputField.css";

const visibilitySvg = require("../../assets/visibility.svg") as string;
const visibilityOffSvg = require("../../assets/visibilityOff.svg") as string;

export default function InputField({ title, placeholder }: { title: string, placeholder: string }) {
    return (
        <div className="inputField">
            <label htmlFor="inputFieldText">{title}</label>
            <div className="inputFieldWrapper">
                <input id="inputFieldText" type="text" placeholder={placeholder} />
                <button>
                    <img src={visibilitySvg} />
                </button>
            </div>
        </div>
    );
}